
.dropbtn {
    background-color: #4CAF50;
    color: white;
    padding: 16px;
    font-size: 16px;
    border: none;

}


.dropdown {
    position: relative;
    display: inline-block;

}


.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f1f1f1;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;

}


.dropdown-content .header_link {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
}


.dropdown-content .header_link:hover {
    background-color: #ddd;
    color: #edef00;
    transition: 0.2s;
}


.dropdown:hover .dropdown-content {
    display: block;
    z-index: 100;


}


.dropdown:hover .dropbtn {
    background-color: #3e8e41;
    transition: ease 1s;
    color:#edef00 ;
}